'use client';

export type Theme = 'light' | 'dark' | 'system';
export type ResolvedTheme = 'light' | 'dark';

const STORAGE_KEY = 'theme';

/**
 * Get the system's preferred color scheme
 */
export function getSystemTheme(): ResolvedTheme {
  if (typeof window === 'undefined') return 'light';
  
  const isDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
  return isDark ? 'dark' : 'light';
}

/**
 * Get the saved theme from localStorage
 */
export function getSavedTheme(): Theme | null {
  if (typeof window === 'undefined') return null;
  
  try {
    const saved = localStorage.getItem(STORAGE_KEY);
    if (saved === 'light' || saved === 'dark' || saved === 'system') {
      return saved;
    }
  } catch (error) {
    console.error('Error reading theme from localStorage:', error);
  }
  
  return null;
}

/**
 * Save theme preference to localStorage
 */
export function saveTheme(theme: Theme): void {
  if (typeof window === 'undefined') return;
  
  try {
    localStorage.setItem(STORAGE_KEY, theme);
  } catch (error) {
    console.error('Error saving theme to localStorage:', error);
  }
}

/**
 * Resolve the actual theme (light or dark) from a theme setting
 */
export function resolveTheme(theme: Theme): ResolvedTheme {
  if (theme === 'system') {
    return getSystemTheme();
  }
  return theme;
}

/**
 * Apply theme to the document
 */
export function applyTheme(theme: ResolvedTheme): void {
  if (typeof window === 'undefined') return;
  
  const root = document.documentElement;
  root.setAttribute('data-theme', theme);
  
  // Also update class for Tailwind's dark mode
  if (theme === 'dark') {
    root.classList.add('dark');
  } else {
    root.classList.remove('dark');
  }
}

/**
 * Initialize theme on page load (call this ASAP to prevent flash)
 */
export function initializeTheme(): ResolvedTheme {
  const saved = getSavedTheme();
  const theme = saved || 'system';
  const resolved = resolveTheme(theme);
  applyTheme(resolved);
  return resolved;
}

/**
 * Set up listener for system theme changes
 */
export function setupSystemThemeListener(callback: (theme: ResolvedTheme) => void): () => void {
  if (typeof window === 'undefined') return () => {};
  
  const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
  
  const handler = (e: MediaQueryListEvent | MediaQueryList) => {
    const saved = getSavedTheme();
    // Only react to system changes if user preference is 'system' or not set
    if (saved === 'system' || saved === null) {
      const newTheme = e.matches ? 'dark' : 'light';
      applyTheme(newTheme);
      callback(newTheme);
    }
  };
  
  // Modern browsers
  if (mediaQuery.addEventListener) {
    mediaQuery.addEventListener('change', handler);
    return () => mediaQuery.removeEventListener('change', handler);
  } 
  // Legacy browsers
  else if (mediaQuery.addListener) {
    mediaQuery.addListener(handler as (e: MediaQueryListEvent) => void);
    return () => mediaQuery.removeListener(handler as (e: MediaQueryListEvent) => void);
  }
  
  return () => {};
}

/**
 * Custom hook for managing theme
 */
export function useThemeManager() {
  if (typeof window === 'undefined') {
    return {
      theme: 'system' as Theme,
      resolvedTheme: 'light' as ResolvedTheme,
      setTheme: () => {},
      toggleTheme: () => {},
    };
  }

  const [theme, setThemeState] = React.useState<Theme>('system');
  const [resolvedTheme, setResolvedTheme] = React.useState<ResolvedTheme>('light');

  React.useEffect(() => {
    // Initialize theme
    const saved = getSavedTheme() || 'system';
    setThemeState(saved);
    const resolved = initializeTheme();
    setResolvedTheme(resolved);

    // Listen for system theme changes
    const cleanup = setupSystemThemeListener((newTheme) => {
      setResolvedTheme(newTheme);
    });

    return cleanup;
  }, []);

  const setTheme = React.useCallback((newTheme: Theme) => {
    setThemeState(newTheme);
    saveTheme(newTheme);
    const resolved = resolveTheme(newTheme);
    applyTheme(resolved);
    setResolvedTheme(resolved);
  }, []);

  const toggleTheme = React.useCallback(() => {
    const newTheme = resolvedTheme === 'light' ? 'dark' : 'light';
    setTheme(newTheme);
  }, [resolvedTheme, setTheme]);

  return {
    theme,
    resolvedTheme,
    setTheme,
    toggleTheme,
  };
}

// React import for the hook
import React from 'react';
